.navbar{
    display: flex;
    justify-content: space-between;
    padding: 0 100px;
    background-color: transparent;
    border-bottom: 1px solid #d9d9d9;
    /*box-shadow: 0 0px #d9d9d9;*/
}
.navbar .title {
    background-color: #d9d9d9;
    padding: 5px;
    border-radius: 5px;
}
.navbar button{
    border: 0 !important;
    background-color: transparent;
    padding: 5px;
}
.navbar-hamburger{
    padding: 20px;
}

.navbar-search-container{
    margin: auto 0;
    width: 100%;
    display: flex;
    justify-content: center;
}
.navbar-settings{
    margin: auto;
    padding: 5px 10px;
}

@media (max-width: 600px) {
    .navbar{
        padding: 0 5px;
    }
    .navbar-hamburger{
        padding: 20px 5px;
    }
}
