.add-card{
    background-color: #f0f0f0;
    padding: 10px;
    /*box-shadow: 2px 2px 2px #d9d9d9;*/
    border-radius: 5px;
    max-width: 600px;
    width: 100%;
}
.add-card-images{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.add-card input{
    width: 90%;
    margin-right: 20px;
    background-color: transparent;
    border: 0;
}
input:focus, button:focus{
    outline-color: transparent !important;
}
.add-card input[type = 'file']::-webkit-file-upload-button{
    width: 30px;
    height: 30px;
    color: transparent;
    /*background-image: url("https://cdn2.iconfinder.com/data/icons/transparent-round-icons/512/camera.png");*/
    /*background-size: 100%;*/
    border: 0 ;
}
.add-card .add-card-upload{
    display: none;
}
.add-card .add-card-tick button{
    border: 0px solid #c1c1c1;
    /*border-radius: 50%;*/
    padding: 1px;
    /*width: 30px;*/
    /*height: 30px;*/
    margin: auto;
}
.add-card .fa-check-square{
    /*background-color: red;*/
    font-size: 20px;

}
.add-card input[type = 'file']{
    margin: auto;
}
.add-card .add-card-tick button i:hover{
    color: darkslategrey;
}
.add-card input[type = "text"]{
padding: 5px;
}
.add-card-save-button{
    background-color: transparent;
    /*border: 1px solid grey;*/
    border-radius: 3px;
    /*color: green;*/
    /*padding-bottom: 3px;*/
    margin: 5px;
    padding: 0 10px 3px 10px;
    border: 0;
    font-size: 110% !important;
}
.add-card-save-button:hover{
    /*border: 1px solid black;*/
    background-color: greenyellow;
}
.add-card-close-button{
    background-color: transparent;
    /*border: 1px solid grey;*/
    border-radius: 3px;
    /*color: red;*/
    padding: 0 10px 3px 10px;
    font-weight: 500;
    font-family: 'Poppins', sans-serif;
    border: 0;
    font-size: 110%;
    margin: 5px;
}
.add-card-close-button:hover{
    /*border: 1px solid black;*/
    background-color: red;
    color: #f0f0f0;
}

button:disabled{
    color: #d9d9d9;
    background-color: transparent;
    border-radius: 3px;
    margin: 5px;
    padding: 3px 10px 3px 10px;
    border: 0;
    font-size: 110% !important;
}
.instruction{
    position: relative;
}
.enter{
    position: absolute;
    bottom: 10px;
    left: -130px;
    font-size: 12px;
    width: 200px;
}
.denter{
    display: none;
    background-color: red;
}
#title{
    font-size: 20px;
}
