.search-bar{
    background-color: #f0f0f0;
    padding: 1px;
    border-radius: 3px;
    display: flex;
    margin-left: 5px;
    justify-content: center;
    max-width: 800px;
    width: 100%;
}
.search-bar input{
    padding: 10px;
    width:88%;
    background-color: transparent;
    border: 0;
}
.search-bar input:focus{
    outline-color: transparent !important;
}
.search-bar img{
    margin: auto 0;
}
