.dark{
    background-color: #454545;
    color: white;
    transition: 0.5s ease-in-out;
}
.bright{
    background-color: white;
    color: #454545;
    transition: 0.5s ease-in-out;
}
.row{
    display: flex;
    flex-wrap: wrap;
    /*margin-left: -10px;*/
    /*margin-right: -10px;*/
    padding: 0 120px;
}

.col{
    flex: 0 0 25.3333333%;
    width: 25.3333333%;
}
@media (max-width: 600px) {
    .col{
        flex: 0 0 100%;
        width: 100%;
    }
    .row{
        padding: 0 20px;
    }
}
@media (min-width: 600px ){
    .col{
        flex: 0 0 50%;
        width: 50%;
    }
    .row{
        padding: 0 20px;
    }
}
@media (min-width: 992px ){
    .col{
        flex: 0 0 23.3333333%;
        width: 23.3333333%;
    }
    .row{
        padding: 0 100px;
    }
}
.cards-container{
    transition: 0.5s;
}

.tooltip .tool{
    display: none;
    position: absolute;
    bottom: -20px;
    border-radius: 2px;
    background-color: #d9d9d9;
    padding: 5px 10px;
    min-width: 50px;
    width: 100%;
    z-index: 1;
    /*border: 1px solid black;*/
}
.tooltip:hover .tool{
    display: block;
}
.tooltip{
    position: relative;
}
