.modal-wrapper {
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.5);
    border: 1px solid #d0cccc;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    color: #000000;
    display: flex;
    justify-content: center;
}
.modal-wrapper h5{
    margin: 0;
    background-color: #d0cccc;
    border-radius: 5px 5px 0 0;
    padding: 5px 15px;
}

.modal-content{
    margin-top: 10vh;
    width: 500px;
    height: 100%;
    max-height: 200px;
    position: relative;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 15px #525252;
}

.modal-content input{
    width: 100%;
    padding: 5px;
    border: 0;
}

.close-modal-btn{
    position: absolute;
    top: 0;
    right: 0;
    color: black;
    cursor: pointer;
    padding: 5px;

}
.modal-body {
    padding: 10px 15px;
    text-align: center;
}
.modal-buttons{
    padding: 0 15px;
    display: flex;
    justify-content: flex-end;
}
.modal-buttons button{
    border: 0;
    background-color: transparent;
    font-family: 'Poppins', sans-serif;
}
.modal-buttons .save{
    font-weight: bold;
    padding: 3px 10px;
}
.modal-buttons .save:hover{
    background-color: #f0f0f0;
    transition: 0.5s;
    border-radius: 3px;
    padding: 3px 10px;
}

.modal-input{
    /*background-color: red;*/
    font-family: 'Poppins', sans-serif;
    font-size: 110%;
    color: #525252;
}
