.card{
    border: 1px solid #d9d9d9;
    min-width: 150px;
    max-width: 100%;
    min-height: 100px;
    max-height: 100%;
    border-radius: 10px;
    position: relative;
    margin: 5px;
}
.card:hover{
    box-shadow: 0px 0px 7px #d9d9d9;
}
.card-options{
    display: flex;
    /*padding-top: 20px;*/
    justify-content: space-between;
    /*padding: 20px 10px 0 10px;*/
    position: absolute;
    width: 80%;
    bottom: 0;
    transition: 0.6s;
}
.card-pinned{
    position: absolute;
    right: 0;
    top: 0;
    transform: rotate(45deg);
}
.card-options:hover{
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    width: 80%;
}
.card-options img{
    padding: 6px;
    border-radius: 50%;
    transition: 1s;
}
.icons{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    height: 25px;width: 25px;
    border-radius: 50%;
    padding: 7px;
}
/*.icons:hover{*/
/*    transition: 0.5s ease-in;*/
/*    background-color: #d9d9d9;*/
/*}*/
.card-options img:hover{
    background-color: #d9d9d9;
    padding: 6px;
    border-radius: 50%;
}
.card-data{
    padding: 12px;
    margin-bottom: 15px;
}
.card-data h6{
    margin: 0 !important;
    font-size: 120%;
}
.card-data li{
    list-style: none;
}
