.sidebar{
    width: 280px;
    height: 90.3vh;
    background-color: transparent;
    padding-right: 10px;
    margin-left: -295px;
    transition: 0.5s ease;
    text-align: left;
    /*border-right: 1px solid #d9d9d9;*/
}
.hide{
    display: none;
}
@media (max-width: 600px) {
    .sidebar{
        position: absolute;
        top: 0;
        left: -295px;
        margin-left: 0;
        z-index: 1000000;
        background-color: #5e5e5e;
    }
    .toggle{
        left: 0;
    }
    .hide{
        display: flex;
    }
}
.sidebar p{
    margin: 0 !important;
}
.toggle{
    margin-left: 0 !important;
    transition: 0.5s ease;
}
.sidebar .active-tab{
    background-color: #d9cac5;
    border-radius: 0 30px 30px 0;
    font-weight: 400;
    transition: 0.7s;
    /*background-color: #d9d9d9;*/
}
.sidebar-section{
    padding: 10px 0 !important;
}
.sidebar h1{
    margin-top: 0 !important;
    padding: 10px;
}
.sidebar div{
    padding: 10px;
}
.sidebar button{
    width: 100%;
    padding: 12px;
    border: 0 !important;
    border-radius: 0 30px 30px 0;
    margin: 10px 0;
    transition: 0s;
    background-color: transparent;
    text-align: left;
}

button:focus{
    outline-color: transparent !important;
}
.sidebar .middle{
    border-bottom: 1px solid #d9d9d9;
    border-top: 1px solid #d9d9d9;
}
.content{
    display: none;
}
.active{
    display: block;
}
@media (max-width: 800px) {
    .sidebar{
        z-index: 9;
        height: 100vh;
    }
}
.sidebar-section button {
    display: flex;
}
.sidebar-section button img{
    margin-top: 0px;
    margin-right: 15px;
}
.content-main{
    /*background-color: red;*/
    min-height: 90.4vh;
    max-height: 100%;
}
